
"use client";

import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { orders } from "@/lib/data";
import type { Order } from "@/lib/types";
import { Button } from "@/components/ui/button";
import Link from "next/link";

const statusColors: { [key: string]: 'default' | 'secondary' | 'destructive' | 'outline' } = {
  'Returned': 'default',
  'Delivered': 'default',
  'Confirmed': 'secondary',
  'Quotation': 'outline',
  'Late': 'destructive'
};

const customerOrders = orders.filter(o => o.customer.name === 'Bob Williams');

const OrderTable = ({ orders }: { orders: Order[] }) => (
    <Table>
        <TableHeader>
            <TableRow>
            <TableHead>Order ID</TableHead>
            <TableHead>Product</TableHead>
            <TableHead>Dates</TableHead>
            <TableHead>Status</TableHead>
            <TableHead className="text-right">Total</TableHead>
            </TableRow>
        </TableHeader>
        <TableBody>
            {orders.map((order) => (
            <TableRow key={order.id}>
                <TableCell className="font-mono text-xs">{order.id}</TableCell>
                <TableCell>{order.productName}</TableCell>
                <TableCell>
                {order.startDate.toLocaleDateString()} - {order.endDate.toLocaleDateString()}
                </TableCell>
                <TableCell>
                <Badge variant={statusColors[order.status]}>{order.status}</Badge>
                </TableCell>
                <TableCell className="text-right">${order.total.toFixed(2)}</TableCell>
            </TableRow>
            ))}
        </TableBody>
    </Table>
);


export default function CustomerDashboardPage() {
  return (
    <div className="space-y-6">
        <div className="flex justify-between items-start">
            <div>
                <h1 className="text-3xl font-bold">Welcome Back, Bob!</h1>
                <p className="text-muted-foreground">Here's a quick look at your recent rental activity.</p>
            </div>
             <Link href="/dashboard/customer/products">
                <Button>Browse Products</Button>
            </Link>
        </div>
        <Card>
            <CardHeader>
                <CardTitle>Recent Orders</CardTitle>
                <CardDescription>A summary of your most recent orders.</CardDescription>
            </CardHeader>
            <CardContent>
                <OrderTable orders={customerOrders.slice(0, 3)} />
                <div className="text-center mt-4">
                    <Link href="/dashboard/customer/orders">
                        <Button variant="link">View All Orders</Button>
                    </Link>
                </div>
            </CardContent>
        </Card>
    </div>
  );
}
